*, *:after, *:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem; }

h6, h5, h4, h3, h2, h1 {
  margin: 0;
  padding: 0; }


p {
  margin: 0;
  padding: 0; }


ul, li, ol {
  margin: 0;
  padding: 0;
  list-style: none; }


a {
  cursor: pointer;
  text-decoration: none; }
